/**
 *  COMMON CSS BLOCK
 */

button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	font-feature-settings: inherit;
	font-variation-settings: inherit;
	font-size: 100%;
	font-weight: inherit;
	line-height: inherit;
	letter-spacing: inherit;
	color: inherit;
	margin: 0;
	padding: 0;
}

.relative {
	position: relative;
}
.z-10 {
	z-index: 10;
}

.gap-1\.5 {
	gap: var(--wp--preset--spacing--1-5);
}

.gap-2 {
	gap: var(--wp--preset--spacing--2);
}

.is-style-line-clamp-1,
.is-style-line-clamp-1 > a {
	overflow: hidden;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
}

.is-style-line-clamp-2,
.is-style-line-clamp-2 > a {
	overflow: hidden;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
}

.hidden-scrollbar {
	-ms-overflow-style: none;
	scrollbar-width: none;
	&::-webkit-scrollbar {
		display: none;
	}
}

//for post reaction button blocks: Like button block, Save button block, View count button block, Comment button block,
.nc-post-reaction-button {
	cursor: pointer;
	line-height: 1;
	text-decoration: none;
	font-size: var(--wp--preset--font-size--xs);
	border: 0px solid transparent;
	background-color: initial;
	background-image: none;
	&.is-actived,
	&:hover {
		color: var(--active-color, inherit) !important;
		border-color: var(--active-border-color, inherit) !important;
		background-color: var(--active-background-color, inherit) !important;
		.wp-block-outermost-icon-block .icon-container {
			background-color: var(
				--active-icon-background-color,
				transparent
			) !important;
			color: var(--active-color, inherit) !important;
			transition-duration: 0.3s;
		}
	}
	&.is-actived {
		svg {
			fill: var(--active-color, inherit);
			color: var(--active-color, inherit);
		}
	}
	.nc__count {
		min-width: 1rem;
		text-align: start;
	}
}
